import type { IArgsMeta, IInletsMeta, IOutletsMeta, IPropsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";
import { Bang } from "../sdk";
import LiveButtonUI, { LiveButtonUIState } from "../ui/button-live";
import LiveObject, { LiveObjectProps } from "./base-live";

export interface LiveButtonProps extends LiveObjectProps {
    bgColor: string;
    activeBgColor: string;
    bgOnColor: string;
    activeBgOnColor: string;
    borderColor: string;
    focusBorderColor: string;
}
export default class LiveButton extends LiveObject<{}, {}, [any], [Bang], [], LiveButtonProps, LiveButtonUIState> {
    static description = "Button";
    static inlets: IInletsMeta = [{
        isHot: true,
        type: "anything",
        description: "Output a bang when anything is received."
    }];
    static outlets: IOutletsMeta = [{
        type: "bang",
        description: "Bang"
    }];
    static props: IPropsMeta<Partial<LiveButtonProps>> = {
        shortName: {
            type: "string",
            default: "live.button",
            description: "Short name to display",
            isUIState: true
        },
        longName: {
            type: "string",
            default: "live.button",
            description: "Long name to display",
            isUIState: true
        },
        max: {
            type: "number",
            default: 1,
            description: "Maximum value",
            isUIState: true
        },
        bgColor: {
            type: "color",
            default: "rgba(90, 90, 90, 1)",
            description: "Background color (inactive)",
            isUIState: true
        },
        activeBgColor: {
            type: "color",
            default: "rgba(195, 195, 195, 1)",
            description: "Background color (active)",
            isUIState: true
        },
        bgOnColor: {
            type: "color",
            default: "rgba(195, 195, 195, 1)",
            description: "Background color (on / inactive)",
            isUIState: true
        },
        activeBgOnColor: {
            type: "color",
            default: "rgba(109, 215, 255, 1)",
            description: "Background color (on / active)",
            isUIState: true
        },
        borderColor: {
            type: "color",
            default: "rgba(80, 80, 80, 1)",
            description: "Border color (unfocus)",
            isUIState: true
        },
        focusBorderColor: {
            type: "color",
            default: "rgba(80, 80, 80, 1)",
            description: "Border color (focus)",
            isUIState: true
        }
    };
    static docs: string = "ui/docs/button.html"
    static UI = LiveButtonUI;
    subscribe() {
        super.subscribe();
        const validateAndUpdateUI = (value = 0, id?: string) => {
            this.validateValue(value, id);
            this.updateUI({ value: this.state.value });
        }
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 1;
            validateAndUpdateUI(0);
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                validateAndUpdateUI(1);
                this.outlet(0, new Bang());
            }
        });
        this.on("changeFromUI", ({ value }) => {
            // const lastValue = this.state.value;
            validateAndUpdateUI(value);

            if (value)
                this.outlet(0, new Bang());
        });
        this.on("updateState", ({ state: { value }, id }) => {
            validateAndUpdateUI(value, id);
            if (value)
                this.outlet(0, new Bang());
        });
    }
}
